import React from 'react'
import './Blog.css'
const Blog = () => {
    return (
        <div>
            <p>您的姓名：</p>
            <input class="names" type="text" placeholder="您的姓名" id="name"/><br/>
            <p>电话号码：</p>
            <input type="tel" name="tel1" required placeholder="请输入手机号" id='tel'/><br/>
            <p>电子邮件：</p>
            <input input type="email" name="email1" placeholder="请输入邮箱地址" id='mail'/><br/>
            <p>留言内容：</p>
            <textarea className='area' id='area'>请输入您想要的商品</textarea><br/>
            <button onClick={()=>{
                var n=document.getElementById("name").value;
                var t=document.getElementById("tel").value;
                var e=document.getElementById("mail").value;
                var a=document.getElementById("area").value;
                var time=(new Date().getTime()).toString();
                var ti = time.slice(0,9);
                var s =localStorage.getItem('id')
                fetch('/madd',{
                    method:'post',
                    mode:'cors',
                    headers:{'Content-Type':'application/json'},
                    body:JSON.stringify({"mid":ti,"mname":n,"mtel":t,"mmail":e,"mcon":a,"vcid":s,"mfin":'未完成'}),
                }).then(alert('提交成功'))}}>提交</button>
        </div>
    ) 
}
export default Blog