import React from 'react'
import { NavLink } from 'react-router-dom'
import './Header.css'
const Header = () => {
    return (
        <header>
            <text>华烁精密</text>
            <nav>
                <NavLink activeClassName='active' exact to='/'>首页</NavLink>
                <NavLink activeClassName='active' to='/tuto'>公司介绍</NavLink>
                <NavLink activeClassName='active' to='/doc'>产品展示</NavLink>
                <NavLink activeClassName='active' to='/detail'>产品案例</NavLink>
                <NavLink activeClassName='active' to='/comm'>联系我们</NavLink>
                <NavLink activeClassName='active' to='/blog'>客户留言</NavLink>
            </nav>
        </header>
    ) 
}
export default Header