import React from 'react'
import {NavLink, Redirect, Switch, withRouter} from 'react-router-dom'
import './Doc.css';
import {RouteWithSubRoutes} from './App'; 
//二级路由里先将子路由拿过来，然后在子路由里再次调用一级路由
const Hooks =({routes})=>{
    return(
        <div className='doc'>
        <ol>
          <li><NavLink to='/hooks/user'>用户管理</NavLink></li>
          <li><NavLink to='/hooks/mas'>花岗石测量座</NavLink></li>
        </ol>
        <Switch>
          {routes.map((route)=><RouteWithSubRoutes {...route}/>)} 
        </Switch>
        </div>
    )
}
export default withRouter(Hooks);