import React from 'react'
import './Login.css';
const Reg = (props) => {
    return (
        <div>
            <div className='box'>
            <p className="tit">
			<span className="brand">用户注册</span>
			<span>用户登录</span>
		    </p>
            <input className="inp" type="text" placeholder="用户名" id="name1" />
		    <p className="notice" id="name"></p>
		    <input className="inp" type="password" placeholder="密码" id="pass1" />
		    <p className="notice" id="pass"></p>
            <div>
			    <input className="inp code" type="text" placeholder="验证码" id="code1" />
                <img className="codeImg" src='http://101.43.154.9:8001/image/2022-5-21_22-59-13_47589.jpg'/>
		    </div>
            <p className="notice" id="code2"></p>
            <button className="btn" 
            onClick={()=>{
                var n=document.getElementById("name1").value;
                var p=document.getElementById("pass1").value;
                var e=document.getElementById("code1").value;
                var time=(new Date().getTime()).toString();
                var ti = time.slice(0,9);
                localStorage.setItem('id',ti)
                var le=n.length;
                if(le<4||le>10){
                    document.getElementById("name").innerHTML="请输入4-10位用户名";
                }
                var len=p.length;
                if(len<6||len>12){
                    document.getElementById("pass").innerHTML="请输入6-12位密码";
                }
                if(e!='zAZfB'){
                    document.getElementById("code2").innerHTML="验证码不正确"; 
                }
                if(le>=4&&le<=10&&len>=6&&len<=12&&e=='zAZfB'){		
                    fetch('/cadd',{
                        method:'post',
                        mode:'cors',
                        headers:{'Content-Type':'application/json'},
                        body:JSON.stringify({"cid":ti,"cps":p,"cname":n}),
                    }).then(alert('您已成功注册')).then(props.history.push('/login'))
                }
                }}>注册</button>
            </div>
        </div>
    ) 
}
export default Reg;