import React from 'react'
import {NavLink, Redirect, Switch, withRouter} from 'react-router-dom'
import './Doc.css';
import {RouteWithSubRoutes} from './App'; 
//二级路由里先将子路由拿过来，然后在子路由里再次调用一级路由
const Doc =({routes})=>{
    return(
        <div className='doc'>
        <Redirect from='/doc' to='/doc/core'/>
        <ol>
          <li><NavLink to='/doc/core'>花岗岩石量仪</NavLink></li>
          <li><NavLink to='/doc/guide'>花岗石测量座</NavLink></li>
          <li><NavLink to='/doc/api'>花岗石机械构件</NavLink></li>
        </ol>
        <Switch>
          {routes.map((route)=><RouteWithSubRoutes {...route}/>)} 
        </Switch>
        </div>
    )
}
export default withRouter(Doc);