import Home from './Home';
import Doc from './Doc';
import Tuto from './Tuto';
import Blog from './Blog';
import Comm from './Comm';
import Login from './Login';
import Hooks from './Hooks';
import Guide from './Guide';
import Core from './Core';
import Api from './Api';
import Detail from './Detail';
import Reg from './Reg';
const routes=[
    {
        path:'/',
        exact:true,
        component:Home
    },
    {
        path:'/doc',
        component:Doc,
        routes:[
            {
                path:'/doc/core',
                component:Core,
            },
            {
                path:'/doc/guide',
                component:Guide,
            },
            {
                path:'/doc/api',
                component:Api,
            }
        ]
    },
    {
        path:'/tuto',
        component:Tuto,
    },
    {
        path:'/blog',
        component:Blog,
        pri:true
    },
    {
        path:'/comm',
        component:Comm
    },
    {
        path:'/login',
        component:Login
    },
    {
        path:'/detail',
        component:Detail
    },
    {
        path:'/reg',
        component:Reg
    }
]
export default routes;